.musicPlayer {
  width: 100%;
  display: flex;
  padding: 10px;
  /* justify-content: space-between; */
}
.songImage {
  width: 80px;
  min-width: 80px;
  height: 80px;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.4);
}
.songImage img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.musicPlayer .songAttributes {
  width: 100%;
  padding-left: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.musicPlayer .songAttributes .top,
.musicPlayer .songAttributes .bottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  /* border: 2px solid red; */
}
.musicPlayer .songAttributes .top .left,
.musicPlayer .songAttributes .top .right {
  display: flex;
  flex-direction: row;
}
.musicPlayer .songAttributes .top .middle {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.musicPlayer .songAttributes .top .left i,
.musicPlayer .songAttributes .top .middle i,
.musicPlayer .songAttributes .top .right i {
  display: inline;
  /* margin: 0px 15px; */
  font-size: 18px;
  color: #f1f1f1;
}

.songAttributes .top .left .loved i {
  color: #49e12e !important;
  filter: drop-shadow((0px 0px 4px #49e12e));
}
.songAttributes .top .middle .back,
.songAttributes .top .middle .forward {
  display: flex;
}
.songAttributes .top .middle .back i:nth-child(2),
.songAttributes .top .middle .forward i:nth-child(1) {
  color: #9a9a9a !important;
}
.songAttributes .top .middle .playPause {
  width: 30px;
  height: 30px;
  min-width: 30px;
  /* border-radius: 100%; */
  /* background: #f1f1f1; */
  display: flex;

  justify-content: center;
  align-items: center;
  color: #49e12e;
  text-align: center;
}

.songAttributes .bottom {
  display: flex;
  margin-top: 20px;
  padding: 0px 15px;
  justify-content: space-between;
}
.songAttributes .bottom .progressBar {
  width: 70%;
  position: relative;
  height: 5px;
  outline: none;
  border: none;
  appearance: none;
  border-radius: 10px;
  background: rgba(255, 255, 255, 0.1);
  cursor: pointer;
  overflow: hidden;
}
/* Chrome and Safari */
.songAttributes .bottom .progressBar:before {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  background: #848484;
  width: var(--player-played);
  height: 100%;
  border-radius: 10px;
  z-index: 2;
  transition: all 0.3s ease;
}
.songAttributes .bottom .progressBar::-webkit-slider-thumb {
  width: 15px;
  height: 15px;
  border-radius: 50%;
  border: none;
  outline: none;
}
/* firefox */
.songAttributes .bottom .progressBar::-moz-range-track {
  /* width: 70%; */
  position: relative;
  height: 5px;
  outline: none;
  border: none;
  appearance: none;
  border-radius: 10px;
  background: rgba(255, 255, 255, 0.1);
  cursor: pointer;
}
.songAttributes .bottom .progressBar::-moz-range-progress {
  background: #848484;
  width: var(--player-played);
  height: 100%;
  border-radius: 10px;
  z-index: 2;
  transition: all 0.3s ease;
}
.songAttributes .bottom .progressBar::-webkit-slider-thumb {
  width: 15px;
  height: 15px;
  border-radius: 50%;
  border: none;
  outline: none;
}
.songAttributes .bottom .currentTime,
.songAttributes .bottom .duration {
  color: #f1f1f1;
  font-size: 14px;
  font-weight: bold;
}
@media screen and (max-width: 746px) {
  .songImage {
    width: 100px;
    min-width: 30px;
  }
  .songAttributes .top {
    /* border: 2px solid red; */
    min-width: 80%;
  }
  .songAttributes .top .left i,
  .songAttributes .top .middle i,
  .songAttributes .top .right i {
    margin: 0px 5px;
    color: #f1f1f1;
  }
  .songAttributes .bottom {
    padding: unset;
  }
  .songAttributes .bottom .progressBar {
    width: 65%;
  }
}
