.App {
  width: 100%;
  height: 100vh;
  position: relative;
  overflow: hidden;
  display: flex;
}
.background {
  position: absolute;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  background: url("./img/background.jpg") no-repeat center;
  background-size: cover;
  filter: grayscale(100);
  z-index: -1;
}
