* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

*::-webkit-scrollbar {
  display: none;
}

.App {
  width: 100%;
  height: 100vh;
  position: relative;
  overflow: hidden;
  display: flex;
}
.background {
  position: absolute;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  background: url(/spotify-clone/static/media/background.91fc2549.jpg) no-repeat center;
  background-size: cover;
  -webkit-filter: grayscale(100);
          filter: grayscale(100);
  z-index: -1;
}

.rightMenu {
  width: 100px;
  height: 100vh;
  background-color: rgba(34, 34, 34, 0.4);
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
  min-width: 60px;
  z-index: 1;
  border-left: 1px solid rgba(255, 255, 255, 0.1);
  transition: all 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding: 20px 0px;
}
.rightMenu .goPro,
.rightMenu .profile {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  cursor: pointer;
}
.rightMenu .goPro i,
.rightMenu .profile i {
  color: #f1f1f1;
  margin: 20px 0;
  text-align: center;
  font-size: 20px;
  position: relative;
}
.rightMenu .goPro i p {
  font-size: 14px;
}
.rightMenu .goPro i p span {
  color: #848484;
}
.rightMenu .goPro i:nth-child(2)::before {
  position: absolute;
  content: "";
  width: 8px;
  height: 8px;
  background: #45ab2e;
  border-radius: 50%;
  top: 0;
  right: 0;
}
.rightMenu .profile .profileImage {
  width: 40px;
  height: 40px;
  overflow: hidden;
  border-radius: 100%;
  box-shadow: 0px 0px 20px rgba(34, 34, 34, 0.4);
  background: #555;
}
.rightMenu .profile .profileImage img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

@media screen and (max-width: 746px) {
  .rightMenu {
    width: 60px;
    min-width: 60px;
  }
}

.musicPlayer {
  width: 100%;
  display: flex;
  padding: 10px;
  /* justify-content: space-between; */
}
.songImage {
  width: 80px;
  min-width: 80px;
  height: 80px;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.4);
}
.songImage img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.musicPlayer .songAttributes {
  width: 100%;
  padding-left: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.musicPlayer .songAttributes .top,
.musicPlayer .songAttributes .bottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  /* border: 2px solid red; */
}
.musicPlayer .songAttributes .top .left,
.musicPlayer .songAttributes .top .right {
  display: flex;
  flex-direction: row;
}
.musicPlayer .songAttributes .top .middle {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.musicPlayer .songAttributes .top .left i,
.musicPlayer .songAttributes .top .middle i,
.musicPlayer .songAttributes .top .right i {
  display: inline;
  /* margin: 0px 15px; */
  font-size: 18px;
  color: #f1f1f1;
}

.songAttributes .top .left .loved i {
  color: #49e12e !important;
  -webkit-filter: drop-shadow((0px 0px 4px #49e12e));
          filter: drop-shadow((0px 0px 4px #49e12e));
}
.songAttributes .top .middle .back,
.songAttributes .top .middle .forward {
  display: flex;
}
.songAttributes .top .middle .back i:nth-child(2),
.songAttributes .top .middle .forward i:nth-child(1) {
  color: #9a9a9a !important;
}
.songAttributes .top .middle .playPause {
  width: 30px;
  height: 30px;
  min-width: 30px;
  /* border-radius: 100%; */
  /* background: #f1f1f1; */
  display: flex;

  justify-content: center;
  align-items: center;
  color: #49e12e;
  text-align: center;
}

.songAttributes .bottom {
  display: flex;
  margin-top: 20px;
  padding: 0px 15px;
  justify-content: space-between;
}
.songAttributes .bottom .progressBar {
  width: 70%;
  position: relative;
  height: 5px;
  outline: none;
  border: none;
  -webkit-appearance: none;
          appearance: none;
  border-radius: 10px;
  background: rgba(255, 255, 255, 0.1);
  cursor: pointer;
  overflow: hidden;
}
/* Chrome and Safari */
.songAttributes .bottom .progressBar:before {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  background: #848484;
  width: var(--player-played);
  height: 100%;
  border-radius: 10px;
  z-index: 2;
  transition: all 0.3s ease;
}
.songAttributes .bottom .progressBar::-webkit-slider-thumb {
  width: 15px;
  height: 15px;
  border-radius: 50%;
  border: none;
  outline: none;
}
/* firefox */
.songAttributes .bottom .progressBar::-moz-range-track {
  /* width: 70%; */
  position: relative;
  height: 5px;
  outline: none;
  border: none;
  appearance: none;
  border-radius: 10px;
  background: rgba(255, 255, 255, 0.1);
  cursor: pointer;
}
.songAttributes .bottom .progressBar::-moz-range-progress {
  background: #848484;
  width: var(--player-played);
  height: 100%;
  border-radius: 10px;
  z-index: 2;
  -moz-transition: all 0.3s ease;
  transition: all 0.3s ease;
}
.songAttributes .bottom .progressBar::-webkit-slider-thumb {
  width: 15px;
  height: 15px;
  border-radius: 50%;
  border: none;
  outline: none;
}
.songAttributes .bottom .currentTime,
.songAttributes .bottom .duration {
  color: #f1f1f1;
  font-size: 14px;
  font-weight: bold;
}
@media screen and (max-width: 746px) {
  .songImage {
    width: 100px;
    min-width: 30px;
  }
  .songAttributes .top {
    /* border: 2px solid red; */
    min-width: 80%;
  }
  .songAttributes .top .left i,
  .songAttributes .top .middle i,
  .songAttributes .top .right i {
    margin: 0px 5px;
    color: #f1f1f1;
  }
  .songAttributes .bottom {
    padding: unset;
  }
  .songAttributes .bottom .progressBar {
    width: 65%;
  }
}

.mainContainer {
  height: 100vh;
  z-index: 1;
  flex-grow: 1;
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
  background: rgba(34, 34, 34, 0.6);
  transition: all 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
}
.banner {
  height: 300px;
  position: relative;
}
.banner .bannerImg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  -webkit-filter: grayscale(100);
          filter: grayscale(100);
}
.banner .bottomLayer {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 5rem;
  background-image: linear-gradient(transparent, rgba(0, 0, 0, 0.9));
}
.banner .content {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  padding: 20px 30px;
  display: flex;
  flex-direction: column;
  z-index: 3;
}
.banner .content .breadCrump {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.banner .content .breadCrump p {
  color: #848484;
  font-size: 14px;
  font-weight: 600;
  letter-spacing: 1px;
}
.banner .content .breadCrump p span {
  color: #f1f1f1;
}
.banner .content .breadCrump i {
  color: #f1f1f1;
  font-size: 18px;
  cursor: pointer;
}
.banner .content .artist {
  margin-top: auto;
  color: #f1f1f1;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.banner .content .artist .left .name {
  display: flex;
  align-items: center;
}
.banner .content .artist .left .name h2 {
  font-size: 50px;
}
.banner .content .artist .left .name img {
  width: 30px;
  margin-left: 10px;
}
.banner .content .artist .left p {
  font-size: 14px;
}
.banner .content .artist .left p i {
  margin-right: 6px;
}
.banner .content .artist .left p span {
  color: #848484;
  margin-left: 5px;
}
.banner .content .artist .right {
  display: flex;
  align-items: center;
}
.banner .content .artist .right a {
  display: flex;
  align-items: center;
  text-decoration: none;
  padding: 10px 30px;
  font-size: 16px;
  color: #f1f1f1;
  margin: 0px 15px;
  border-radius: 100vh;
  font-weight: bold;
  text-align: center;
}
.banner .content .artist .right a i {
  margin-right: 10px;
  font-size: 12px;
}
.banner .content .artist .right a:nth-child(1) {
  background: #39aa24;
}
.banner .content .artist .right a:nth-child(2) {
  background: rgba(0, 0, 0, 0.3);
  margin-left: 0;
  border: 1px solid rgba(255, 255, 255, 0.1);
}
.banner .content .artist .right a:hover {
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.4);
}
.mainContainer .menuList {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 25px;
  background: #2c2c2c;
}
.menuList ul {
  display: flex;
  align-items: center;
}
.menuList ul li {
  list-style: none;
  margin: 0 20px;
  position: relative;
}
.menuList ul li a {
  text-decoration: none;
  color: #848484;
  font-size: 14px;
  font-weight: bold;
}
.menuList ul li::before {
  position: absolute;
  content: "";
  bottom: -20px;
  left: 10px;
  width: 0px;
  height: 5px;
  border-radius: 5px;
  background: #f1f1f1;
}
.menuList ul li:hover::before,
.menuList ul li.active::before {
  width: 20px;
  transition: width 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
}
.menuList ul li:hover a,
.menuList ul li.active a {
  color: #f1f1f1;
  transition: 0.3s;
}
.menuList p {
  color: #f1f1f1;
  font-size: 14px;
}
.menuList p i {
  margin-right: 6px;
  font-size: 12px;
}
.menuList p span {
  /* display: flex; */
  font-size: 12px;
  text-align: center;
  color: #848484;
  margin-left: 6px;
}

/* main audio list style */
.audioList {
  padding: 10px 30px;
}
.audioList .title {
  font-size: 18px;
  color: #f1f1f1;
}
.audioList .title span {
  color: #848484;
  font-size: 12px;
}
.songsContainer {
  height: 160px;
  overflow-y: scroll;
  overflow-x: hidden;
}
.songs {
  display: flex;
  align-items: center;
  font-size: 14px;
  color: #848484;
  width: 100%;
  margin: 10px 0;
  padding: 5px;
  cursor: default;
}
.songs:hover,
.songs.active {
  color: #f1f1f1;
  transition: 0.3s;
}
.count {
  margin-right: 10px;
}
.song {
  display: flex;
  align-items: center;
  width: 100%;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}
.song .imgBox {
  width: 45px;
  height: 45px;
  min-width: 45px;
  border-radius: 0.5em;
  background: #555;
  margin-right: 10px;
  overflow: hidden;
}
.song .imgBox img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.song .section {
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: center;
}
.song .section .songName {
  margin-left: auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 50%;
}
.song .section .hits {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.song .section .hits .hit,
.song .section .hits .hit .duration {
  margin-right: 15px;
}
.song .section .hits p {
  margin-right: 15px;
}
.song .section .hits .hit i,
.song .section .hits .hit .duration i {
  font-size: 12px;
  margin-right: 5px;
}
.song .section .hits .favourite i {
  color: #49e12e;
  -webkit-filter: drop-shadow(0px 0px 4px #49e12e);
          filter: drop-shadow(0px 0px 4px #49e12e);
  cursor: pointer;
}

@media screen and (max-width: 550px) {
  .mainContainer {
    min-width: 300px;
  }
  .content {
    padding: 10px 15px;
  }
  .banner .content .artist .left .name h2 {
    font-size: 25px;
  }
  .banner .content .artist .right a {
    padding: 5px 15px;
    font-size: 12px;
    margin: 0 10px;
  }
  .menuList {
    padding: 5px 10px;
  }
  .menuList ul li {
    margin: 0 5px;
  }
  .menuList p {
    font-size: 12px;
  }
  .menuList p i {
    font-size: 10px;
  }
  .audioList {
    padding: 10px 5px;
  }
  .audioList .songs .song .section {
    flex-direction: column;
    align-items: flex-start;
  }
  .audioList .songs .song .section .songName {
    width: 100%;
  }
  .audioList .songs .song .section .hits {
    justify-content: space-between;
    margin-left: 0;
    width: 100%;
    margin-top: 10px;
  }
}

